











































import Vue from 'vue'
import { mapGetters } from 'vuex'
export default Vue.extend({
  name: 'Course1',
  mounted () {
    this.defaultNumar = this.numar
  },
  props: {
    numar: {
      type: Number,
      default: () => -1
    }
  },
  components: {
  },
  data () {
    const isNegative = false
    const text = 'Parola gresita'
    const participanti:string[] = []
    return {
      defaultNumar: 0,
      isNegative: isNegative,
      text: text,
      parolaCorecta: false,
      participant: '',
      participanti: participanti,
      part: ''
    }
  },
  methods: {
    emiteEveniment () {
      this.$emit('trimite-parola', this.text)
    },
    adaugaParticipant () {
      this.participanti.push(this.participant)
      this.participant = ''
    },
    cresteNumar () {
      this.defaultNumar = this.numar + 1
    },
    scadeNumar () {
      if (!this.isNegative) {
        this.defaultNumar = this.numar - 1
      }
    }
  },
  computed: {
    part2 ():string {
      return this.participant + this.numar + this.isNegative
    },
    ...mapGetters({
      numere: 'store/numere'
    })
  },
  watch: {
    numar () {
      if (this.numar <= 0) {
        this.isNegative = true
      } else {
        this.isNegative = false
      }
    },
    text () {
      if (this.text === 'parola') {
        this.parolaCorecta = true
      } else {
        this.parolaCorecta = false
      }
    }
  }
})
