






import Vue from 'vue'
export default Vue.extend({
  name: 'App',
  mounted: function () {
    const token = window.localStorage.getItem('token')
    if (token && token !== '' && this.$route.path !== '/dashboard') {
      this.$router.push('/dashboard')
    } else if (this.$route.path !== '/login') {
      this.$router.push('/login')
    }
  },
  data () {
    return {}
  }
})
