



































































import Vue from 'vue'
import { mapGetters } from 'vuex'
export default Vue.extend({
  name: 'Dashboard',
  computed: {
    ...mapGetters({
      numere: 'store/numere'
    })
  },
  data () {
    const number = '13'
    const pageArray = [
      {
        name: 'Dashboard',
        path: '/dashboard',
        icon: 'mdi-home'
      },
      {
        name: 'Course 1',
        path: '/course1',
        icon: 'mdi-camera-account'
      },
      {
        name: 'Course 2',
        path: '/course2',
        icon: 'mdi-folder-account'
      }
    ]
    return {
      number: number,
      drawer: false,
      pageArray: pageArray
    }
  },
  methods: {
    logout () {
      window.localStorage.setItem('token', '')
      this.$router.push('/login')
    },
    addNumber () {
      this.$store.commit('store/numere', this.number)
    },
    replaceArray () {
      this.$store.dispatch('store/replace', [420, 69, 12, 13, 201])
    },
    change (to:string) {
      if (to === 'course1') {
        this.$router.push('/course1')
      } else if (to === 'course2') {
        this.$router.push('/course2')
      } else if (to === 'home') {
        this.$router.push('/dashboard')
      }
    }
  },
  watch: {}
})
