
































































import Vue from 'vue'
import { mapGetters } from 'vuex'
export default Vue.extend({
  name: 'Login',
  computed: {
    ...mapGetters({
      numere: 'store/numere'
    })
  },
  data () {
    return {
      username: '',
      password: '',
      inc: false
    }
  },
  methods: {
    async loginFunction ():Promise<void> {
      try {
        if (this.username === 'user' && this.password === 'parola') {
          this.$router.push('/dashboard')
          window.localStorage.setItem('token', 'tokenSecret')
        } else {
          this.inc = true
          setTimeout(() => {
            this.inc = false
          }, 5000)
        }
      } catch (error) {
        console.error(error)
      }
    }
  },
  watch: {}
})
